
import { defineComponent } from "vue";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import { ICM208001SC00_RES, ICM208001SC00_REQ_SEARCH } from "@/shared/type/ICM208001SC00.type";
import { CommonCodeType } from '@/shared/type/commonCodeType';
import pagination from "@/shared/components/Pagination/Pagination.vue";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

export default defineComponent({
    name: "icm208001sc00",

    components: {
        pagination,
        BreadCrumb,
        SelectOption
    },
    data() {
        const headers = [
            { label: "No." },
            { label: "View" },
            { label: "Terms and Condition Category Name" },
            { label: "Terms and Condition Code" },
            { label: "Terms and Condition Title" },
            { label: 'Status' },
            { label: "Created date" },
            { label: "Display" }
        ];
        return {
            headers,
            dataTable: [] as ICM208001SC00_RES[],
            displayYN: [] as CommonCodeType[],
            searchRole: {
                displayYN: "",
                searchValue: "",
                pageNumber: 1,
                pageSize: PAGE_SIZE,
                statusCode: '',
                searchTypeCode: '00'
            } as ICM208001SC00_REQ_SEARCH,
            totalCount: 0 as number,
            startingIndex: 1 as number,
            isResetPage: false as boolean
        };
    },

    mounted() {
        this.requestTermsAndConditionsList(false);
    },

    methods: {
        async requestTermsAndConditionsList(isSearch: boolean) {
            const body = {
                displayYN: this.searchRole.displayYN,
                searchValue: this.searchRole.searchValue.trim(),
                statusCode: this.searchRole.statusCode,
                searchTypeCode: this.searchRole.searchTypeCode,
                pageNumber: isSearch ? this.searchRole.pageNumber = 1 : this.searchRole.pageNumber,
                pageSize: isSearch ? this.searchRole.pageSize = PAGE_SIZE : this.searchRole.pageSize,
            };
            if (isSearch) {
                this.searchRole.pageNumber = 1
                this.isResetPage = true
            }
            await this.$request.getRequest('ICM20800111', body, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.dataTable = res.body.list.map((data) => {
                            return {
                                boardCategoryName: data.boardCategoryName,
                                boardCategoryCode: `${data.boardCategoryCode} : ${data.boardCategoryName}`,
                                firstRegisterDate: data.firstRegisterDate ? this.$datetime.formatStringToStringDate(data.firstRegisterDate) : "",
                                title: data.title,
                                termConditionCode: data.termConditionCode,
                                displayYN: this.$codeConverter.convertValue("YnType", data.displayYN),
                                statusCode: `${data.statusCode} : ${this.$codeConverter.convertValue("ContentManangementStatusCode", data.statusCode)}`,
                            }
                        })
                        this.totalCount = res.body.totalCount;
                    }
                });
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.searchRole.pageNumber = pageNumber;
            this.requestTermsAndConditionsList(false);
        },

        handleStartIndex(startIndex: number) {
            this.startingIndex = startIndex;
        },

        handleClearSearch() {
            this.searchRole.displayYN = this.searchRole.searchValue = this.searchRole.statusCode = ''
            this.isResetPage = true;
            this.searchRole.pageNumber = 1
            this.searchRole.pageSize = PAGE_SIZE
            this.requestTermsAndConditionsList(false)
        },

        handleClickAction(actionType: string, rowData?: ICM208001SC00_RES) {
            switch (actionType) {
                case 'register':
                    this.$router.push({
                        path: `ICM208001SC01`,
                        query: { type: "register" }
                    });
                    break;
                case 'detail':
                    this.$router.push({
                        path: `ICM208001SC01`,
                        query: {
                            type: "edit",
                            termConditionCode: rowData?.termConditionCode
                        }
                    });
                    break;
                case 'search':
                    this.requestTermsAndConditionsList(true);
                    break;
                case 'clearSearch':
                    this.handleClearSearch();
            }
        },

        statusConverter(statusCode): string {
            switch (statusCode) {
                case 'Yes':
                    return 'yes'
                case 'No':
                    return 'no'
                default:
                    return 'info'
            }
        },
    },
});
